<template>
  <div id="discover-brands-component">
    <el-row :gutter="40">
      <el-col :span="6" class="filter-aside-container">
        <h2 class="zoom-title">Advanced Search</h2>
        <div v-if="optionsData" class="selects-container">
          <div class="select-item-container">
            <h3>Material</h3>
            <el-select
              v-model="value1"
              multiple
              filterable
              placeholder="Select"
            >
              <el-option
                v-for="item in optionsData.materials"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="select-item-container">
            <h3>Sports</h3>
            <el-select
              v-model="value2"
              multiple
              filterable
              placeholder="Select"
            >
              <el-option
                v-for="item in optionsData.sports"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="select-item-container">
            <h3>Social Media Performance</h3>
            <el-select
              v-model="value3"
              multiple
              filterable
              placeholder="Select"
            >
              <el-option
                v-for="item in optionsData.social_media_performances"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="select-item-container">
            <h3>E-commerce Performance</h3>
            <el-select
              v-model="value4"
              multiple
              filterable
              placeholder="Select"
            >
              <el-option
                v-for="item in optionsData.e_commerce_performances"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="select-item-container">
            <h3>Sustainability Performance</h3>
            <el-select
              v-model="value5"
              multiple
              filterable
              placeholder="Select"
            >
              <el-option
                v-for="item in optionsData.sustainability_performances"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </el-col>
      <el-col :span="18">
        <BrandTable
          max-height="800px"
          :data="tableData"
          @reload="onReload"
        ></BrandTable>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, computed, watch } from "vue";
import BrandTable from "@/components/BrandTable.vue";
import { getBrandsTable, getDiscoverBrandOptions } from "@/apis/brand";
import {
  BrandTableItemResponse,
  DiscoverOptionsResponse
} from "@/typings/brandInsight";
import { loading } from "@/utils";

const Component = defineComponent({
  name: "DiscoverBrands",
  components: {
    BrandTable
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup() {
    const value1 = ref([]);
    const value2 = ref([]);
    const value3 = ref([]);
    const value4 = ref([]);
    const value5 = ref([]);
    const tableData = ref<BrandTableItemResponse[]>([]);
    const optionsData = ref<DiscoverOptionsResponse | null>(null);

    const tags = computed((): string => {
      return [
        ...value1.value,
        ...value2.value,
        ...value3.value,
        ...value4.value,
        ...value5.value
      ].join(",");
    });

    const fetchOptions = async () => {
      const { data } = await getDiscoverBrandOptions();
      optionsData.value = data;
    };

    const fetchTable = async () => {
      const { data } = await getBrandsTable({ tags: tags.value });
      tableData.value = data;
    };

    watch([tags], () => {
      fetchTable();
    });

    const init = async () => {
      const loadingInstance = loading();
      try {
        await fetchOptions();
        await fetchTable();
      } finally {
        loadingInstance.close();
      }
    };

    const onReload = () => {
      fetchTable();
    };

    onMounted(init);

    return {
      value1,
      value2,
      value3,
      value4,
      value5,

      optionsData,
      tableData,

      onReload
    };
  }
});

export default Component;
</script>

